import { useTranslation } from "react-i18next";
import style from "../../InstituteFeedback/InstituteFeedback.module.css";
import { Link, useLocation, useParams } from "react-router-dom";

const AutomateInstituteDepartmentTypeCard = ({ dType }) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  return (
    <Link
      to={`/q/${params.username}/automate/institute?type=stream`}
      state={{
        ...getQuery.state,
        departmentId: dType?._id,
        departmentName: dType?.name,
      }}
    >
      <div className={style.sf_card}>
        <div className={style.sf_card_contatiner}>
          <div className={style.sf_card_contatiner_inner}>
            <h6
              style={{
                marginBottom: "0",
              }}
            >
              {t("department_type")}
              {": "}
              {dType?.name ?? ""}
            </h6>
          </div>
        </div>
      </div>
    </Link>
  );
};

export default AutomateInstituteDepartmentTypeCard;
